export default function MessagesPage() {
  const [selectedChat, setSelectedChat] = useState(null);
  const [selectedView, setSelectedView] = useState("direct");
  const [isCreateGroupOpen, setIsCreateGroupOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const handleSearch = (term) => {
    setSearchTerm(term);
  };

  return (
    <div className="h-[calc(100vh-4rem)] flex">
      {/* Main sidebar with chat list */}
      <div className="w-80 border-r bg-background flex-shrink-0">
        <div className="flex h-full flex-col">
          {/* Search and actions header */}
          <div className="p-4 border-b space-y-4">
            <div className="flex gap-2">
              <div className="relative flex-1">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search messages..."
                  className="pl-8"
                  value={searchTerm}
                  onChange={(e) => handleSearch(e.target.value)}
                />
              </div>
              <Button
                size="icon"
                variant="outline"
                className="shrink-0"
                onClick={() => setIsCreateGroupOpen(true)}
              >
                <Edit className="h-4 w-4" />
              </Button>
            </div>

            <Tabs
              defaultValue="direct"
              className="w-full"
              onValueChange={setSelectedView}
            >
              <TabsList className="grid w-full grid-cols-4">
                <TabsTrigger value="direct">
                  <Users className="h-4 w-4" />
                </TabsTrigger>
                <TabsTrigger value="generations">
                  <GraduationCap className="h-4 w-4" />
                </TabsTrigger>
                <TabsTrigger value="departments">
                  <Hash className="h-4 w-4" />
                </TabsTrigger>
                <TabsTrigger value="starred">
                  <Star className="h-4 w-4" />
                </TabsTrigger>
              </TabsList>
            </Tabs>
          </div>

          {/* Chat list based on selected view */}
          <ChatSidebar
            directMessages={mockDirectMessages}
            groups={mockGroups}
            selectedView={selectedView}
            onSelectChat={setSelectedChat}
            searchTerm={searchTerm}
          />
        </div>
      </div>

      {/* Main chat area */}
      <div className="flex-1 flex bg-background">
        {selectedChat ? (
          <ChatView chat={selectedChat} />
        ) : (
          <div className="flex-1 flex items-center justify-center text-muted-foreground">
            Select a conversation to start messaging
          </div>
        )}
      </div>

      {/* Dialogs */}
      <CreateGroupDialog
        open={isCreateGroupOpen}
        onOpenChange={setIsCreateGroupOpen}
      />
    </div>
  );
}